@media screen and (max-width:767px){

	.navigation-wrap .navbar-default .nav li a{
		padding: 10px;
		margin-left: 4px;
		font-size: 8px;
	}
	.navbar-header {
		float: none;
	}

	.navbar-collapse.collapse {
		display: none !important;
	}
	.navbar-nav {
		float: none !important;
		margin: 15px;
	}
	.navbar-collapse.collapse.in {
		display: block !important;
	}
	.container-fluid > .navbar-collapse {
		margin-left: -15px;
		margin-right: -15px;
	}
	.navbar-collapse {
		border-top: none;
		-webkit-box-shadow: none;
		box-shadow: none;

	}
	.navbar-default .navbar-toggle{
		width:auto;
		border: none;
	}

	.navbar .navbar-toggle, .innerHeader .navbar .navbar-toggle {
		margin-top: 10px;
		transition: all 0.2s;
		-moz-transition: all 0.2s;
		-ms-transition: all 0.2s;
		-o-transition: all 0.2s;
		-webkit-transition: all 0.2s;
	}
	.navbar-toggle .icon-bar {
		width: 35px;
		height: 4px;
		transition: all 0.2s;
		-moz-transition: all 0.2s;
		-ms-transition: all 0.2s;
		-o-transition: all 0.2s;
		-webkit-transition: all 0.2s;
		background: #c41230;
	}
	.navbar-toggle.collapsed .icon-bar:nth-child(2) {
		transform: rotate(0);
		-moz-ransform: rotate(0);
		-ms-transform: rotate(0);
		-o-transform: rotate(0);
		-webkit-transform: rotate(0);
	}
	.navbar-toggle.collapsed .icon-bar:nth-child(3) {
		opacity: 1;
	}
	.navbar-toggle.collapsed .icon-bar:nth-child(4) {
		transform: rotate(0);
		-moz-ransform: rotate(0);
		-ms-transform: rotate(0);
		-o-transform: rotate(0);
		-webkit-transform: rotate(0);
	}
	.navbar-toggle .icon-bar:nth-child(2) {
		transform: rotate(45deg);
		-moz-transform: rotate(45deg);
		-ms-transform: rotate(45deg);
		-o-transform: rotate(45deg);
		-webkit-transform: rotate(45deg);
		transform-origin: 10% 90%;
		-moz-transform-origin: 10% 90%;
		-ms-transform-origin: 10% 90%;
		-o-transform-origin: 10% 90%;
		-webkit-transform-origin: 10% 90%;
	}
	.navbar-toggle .icon-bar:nth-child(3) {
		opacity: 0;
	}
	.navbar-toggle .icon-bar:nth-child(4) {
		transform: rotate(-45deg);
		-moz-transform: rotate(-45deg);
		-ms-transform: rotate(-45deg);
		-o-transform: rotate(-45deg);
		-webkit-transform: rotate(-45deg);
		transform-origin: 25% 90%;
		-moz-transform-origin: 25% 90%;
		-ms-transform-origin: 25% 90%;
		-o-transform-origin: 25% 90%;
		-webkit-transform-origin: 25% 90%;
	}
	.navbar-default .navbar-toggle .icon-bar{
		background-color:#c41230;	
	}
	.navbar-default .navbar-toggle:hover, .navbar-default .navbar-toggle:focus {
		background: none;
	}
	.main-header .top-bar .list{
		text-align: center;
	}
	.main-header .top-bar .list li{
		padding: 15px;
	}
	.navigation-wrap .navbar-default .navbar-brand{
		padding: 8px 20px;
	} 
	.banner .logo-wrap{
		max-width: 60%;
		padding: 50px 20px 100px;
	}
	.banner .logo-wrap img{
		max-width: 50%;
	}
	.banner .banner-box p{
		font-size: 16px;
	}
	.banner{
		min-height: 380px;
	}
	.products-main .product-box .plus-en{
		margin-left: -10%;
	}
	.des-question .des-content .contact-us{
		font-size: 14px;
		padding: 15px 40px;
	}
	.bottom-footer .footer-menu ul{
		text-align: left;
	}
	.bottom-footer .footer-menu ul li  a{
		line-height: 2;
	}
	.bottom-footer .footer-menu ul li:first-child a{
		margin-left: 0px;
	}
	.bottom-footer{
		padding: 0px 0px 30px;
	}
	.navbar-nav li{
		margin-bottom: 10px;
	}
	.navbar-nav > li > .dropdown-menu{
		position: static;
		left: auto;
		top: auto;
		min-width: 100%;
		box-shadow: none;
		border: none;
	}
	.navigation-wrap .navbar-default .nav li a{
		display: block;
		margin-left: 0px;
		text-align: center;
	}

}
